import React from "react";

const Button = (props) => {
  let shouldDisable = "";
  if (props.disabled) shouldDisable = " disabled";
  else shouldDisable = " shadow ";
  let text = null;
  if (props.children) {
    text = props.children;
  } else {
    text = props.text;
  }

  return (
    <button
      onClick={props.clicked}
      className={props.class + " " + shouldDisable}
    >
      {text}
    </button>
  );
};

export default Button;
