import React from "react";

const BannerTag = (props) => {
  let myclass = "";
  let passedIcon = null;
  const step = parseInt(props.step);
  const index = parseInt(props.index);
  if (step > index) {
    myclass = "bannerTagPassed";
    passedIcon = (
      <span className="BannerPassedIcon">
        <i className="fa fa-check-circle"></i>
      </span>
    );
  } else if (step === index) myclass = "bannerTagCurrent";
  else if (step < index) myclass = "bannerTagDisable";

  return (
    <div
      className={myclass + " bannerTag rounded-pill px-4"}
      style={{ zIndex: "2" }}
    >
      {props.text}
      {passedIcon}
    </div>
  );
};

export default BannerTag;
