import React, { Component } from "react";
import logo from "../asset/images/logo.png";
import { Link } from "react-router-dom";
import Button from "../components/Button";
import toPersianDigit from "../utility/toPersianDigit";
import { connect } from "react-redux";

class Header extends Component {
  render() {
    let Cart = 0;
    let showCartNum = "d-none";
    if (this.props.globalPizza.length) {
      Cart++;
    }
    if (this.props.globalExtra.length) {
      Cart += this.props.globalExtra.length;
    }
    if (Cart > 0) {
      showCartNum = "d-block";
    } else {
      showCartNum = "d-none";
    }

    return (
      <>
        <div className="header">
          <nav className="navbar navbar-expand-md navbar-dark fontSize pt-3">
            <button
              className="navbar-toggler"
              type="button"
              data-toggle="collapse"
              data-target="#navbarNav"
            >
              <span className="navbar-toggler-icon"></span>
            </button>
            <div className="collapse navbar-collapse" id="navbarNav">
              <ul className="navbar-nav">
                <li className="nav-item active">
                  <Link to={process.env.PUBLIC_URL + "/"} className="link">
                    <a className="nav-link mx-2" href="#">
                      صفحه نخست
                      <span className="sr-only">(current)</span>
                    </a>
                  </Link>
                </li>
                <li className="nav-item">
                  <a className="nav-link  mx-2" href="#">
                    سفارش از منو
                    <span className="sr-only">(current)</span>
                  </a>
                </li>
                <li className="nav-item">
                  <a className="nav-link  mx-2" href="#">
                    پیگیری سفارش
                  </a>
                </li>
                <li className="nav-item">
                  <a className="nav-link  mx-2" href="#">
                    عضویت / ورود
                  </a>
                </li>
              </ul>
            </div>
          </nav>
          <div className="row pb-5 w-100 ">
            <div className="col align-self-end text-center ">
              <div className="d-none d-md-block">
                <Link to={process.env.PUBLIC_URL + "/"} className="link">
                  <Button
                    text="پیتزای خودت را بساز!"
                    class="btn px-5 py-2 mt-4 mb-4 ml-auto btnNext rounded-pill "
                    disabled={false}
                  />
                </Link>
              </div>
              <div className="d-block d-md-none">
                <Link to={process.env.PUBLIC_URL + "/"} className="link">
                  <Button
                    text="پیتزا ساز"
                    class="btn  py-2 mt-4 mb-4 ml-auto btnNext rounded-pill "
                    disabled={false}
                  />
                </Link>
              </div>
            </div>
            <div className="col ">
              <Link to={process.env.PUBLIC_URL + "/"} className="link">
                <img
                  src="images/logo.png"
                  className="headerLogo mx-auto mb-5 mt-3 d-block"
                />
              </Link>
            </div>
            <div className="col align-self-end text-center">
              <div className="position-relative ">
                <Link to={process.env.PUBLIC_URL + "/order"} className="link">
                  <Button
                    text="basket"
                    class="btn px-3 py-2 mt-4 mb-4  btnBasket rounded-pill "
                    disabled={false}
                  >
                    <img
                      src="images/shoppingCart.png"
                      style={{ width: "26px" }}
                    />
                  </Button>
                </Link>
                <div
                  className={
                    "position-absolute rounded-circle shoppingCartBadge " +
                    showCartNum
                  }
                >
                  {toPersianDigit(Cart.toString())}
                </div>
              </div>
            </div>
          </div>
        </div>
      </>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    globalPizza: state.pizza,
    globalExtra: state.extra,
    globalPrice: state.price,
    globalPizzaPrice: state.pizzaPrice,
    globalPizzaNumber: state.pizzaNumber,
    globalPizzaName: state.pizzaName,
  }; //reducer states
};

const mapDispatchToProps = (dispatch) => {
  return {};
};

export default connect(mapStateToProps, mapDispatchToProps)(Header);
