import React from "react";
import BannerTag from "./BannerTag";

const Banner = (props) => {
  if (props.step)
    return (
      <div className="d-none d-md-block bannerBg shadow">
        <div className="d-none d-md-flex flex-row justify-content-md-between align-items-center py-3 position-relative banner">
          <div className="bannerLine"></div>
          <BannerTag text="انتخاب خمیر" index="1" step={props.step} />
          <BannerTag text="انتخاب سس کف" index="2" step={props.step} />
          <BannerTag text="انتخاب پنیر" index="3" step={props.step} />
          <BannerTag text="انتخاب پروتئین" index="4" step={props.step} />
          <BannerTag text="انتخاب سبزیجات" index="5" step={props.step} />
        </div>
      </div>
    );
};

export default Banner;
