import React, { Component } from "react";
import toPersianDigit from "../utility/toPersianDigit";

class ConstNumber extends Component {
  state = {};

  render() {
    let disableNumBtn = "";
    if (this.props.number < 1) {
      disableNumBtn = "disable";
    }

    return (
      <>
        <div className="row justify-content-center">
          <div className="col col-md-3 p-0">
            <div
              className=" plusNumber mr-auto rounded-circle shadow"
              onClick={this.props.plusClicked}
            >
              +
            </div>
          </div>
          <div className="col col-md-5 px-1">
            <div className="textNumber rounded-pill ">
              {toPersianDigit(this.props.number.toString())}
            </div>
          </div>
          <div className="col col-md-3  p-0">
            <div
              className={
                " plusNumber ml-auto rounded-circle shadow " + disableNumBtn
              }
              onClick={this.props.minusClicked}
            >
              -
            </div>
          </div>
        </div>
        <div className="row justify-content-center mt-3 textPrice">
          <div>
            {toPersianDigit(
              (this.props.number * this.props.itemPrice).toString()
            )}{" "}
            تومان{" "}
          </div>
        </div>
      </>
    );
  }
}

export default ConstNumber;
