import React, { Component } from "react";
import { connect } from "react-redux";
import CostNumber from "./components/CostNumber";
import * as ingredientNames from "./components/ingredientNames";
import { Link } from "react-router-dom";
import Button from "./components/Button";
import Banner2 from "./components/Banner2";
import toPersianDigit from "./utility/toPersianDigit";

class Order extends Component {
  state = {};

  onPlusHandler = () => {
    this.props.OnIncPizzaNum();
    //console.log("Gn", this.props.globalPizzaNumber);
  };

  onMinusHandler = () => {
    if (this.props.globalPizzaNumber > 0) {
      this.props.OnDecPizzaNum();
    }
  };

  onPlusExtraHandler = (extraName, extraPrice) => {
    this.props.OnIncExtraNum(extraName, extraPrice);
  };
  onMinusExtraHandler = (extraName, extraPrice) => {
    for (let x of this.props.globalExtra) {
      if (x.extraName == extraName) {
        if (x.extraNum < 2) {
          this.props.OnRemoveExtr(extraName, extraPrice);
        }
        if (x.extraNum > 0) {
          this.props.OnDecExtraNum(extraName, extraPrice);
        }
        break;
      }
    }
  };

  onBlurHandler = (event) => {
    if (event.target.value != "") {
      this.props.OnNamePizza(event.target.value);
    }
  };

  render() {
    let pizza = null;
    let extra = null;
    if (this.props.globalPizza.length) {
      const pizzaIngreds = this.props.globalPizza;
      pizza = pizzaIngreds.map((item, index) => {
        return (
          <img
            key={index}
            src={"images/" + ingredientNames[item].image}
            className="d-block mx-auto centerAbsolute"
            style={{
              width: "110px",
              minWidth: "110px",
              maxWidth: "110px",
              zIndex: index + 100,
            }}
          />
        );
      });
    }

    let extraList = null;
    if (this.props.globalExtra.length > 0) {
      extraList = this.props.globalExtra.map((item, index) => {
        return (
          <div
            className="row mx-3 my-1 p-1"
            key={ingredientNames[item.extraName].state}
          >
            <div className="col col-lg-10 col-md-9 ">
              <div
                className="row orderBg rounded shadow my-2 mx-1"
                style={{ height: "100%" }}
              >
                <div className="col col-lg-3 col-md-4 ">
                  <div className="pisition-relative orderPizzaContainer my-auto">
                    <img
                      src={"images/" + ingredientNames[item.extraName].image}
                      className="d-block mx-auto pizzaPan centerAbsolute rounded-circle"
                      style={{
                        zIndex: 9,
                        width: "100px",
                        minWidth: "100px",
                        maxWidth: "100px",
                      }}
                    />
                  </div>
                </div>
                <div className="col col-lg-9 col-md-8 text-center my-auto px-3">
                  <div> {ingredientNames[item.extraName].farsiName}</div>
                </div>
              </div>
            </div>
            <div className="col col-lg-2 col-md-3 ">
              <div
                className="row orderBg rounded shadow my-2 ml-1"
                style={{ height: "100%" }}
              >
                <div className="col col-lg-12 my-auto text-center">
                  <CostNumber
                    price={this.props.globalPrice}
                    number={item.extraNum}
                    itemPrice={ingredientNames[item.extraName].price}
                    plusClicked={() =>
                      this.onPlusExtraHandler(
                        item.extraName,
                        ingredientNames[item.extraName].price
                      )
                    }
                    minusClicked={() =>
                      this.onMinusExtraHandler(
                        item.extraName,
                        ingredientNames[item.extraName].price
                      )
                    }
                  />
                </div>
              </div>
            </div>
          </div>
        );
      });
    }

    return (
      <>
        <Banner2 step="3" />
        <div className="stepTitle mt-5">ثبت نهایی سفارش</div>
        <div className="orderFrame">
          <div className="row mx-3 my-1 p-1">
            <div className="col col-lg-10 col-md-9 ">
              <div
                className="row orderBg rounded shadow my-2 mx-1"
                style={{ height: "100%" }}
              >
                <div className="col col-lg-3 col-md-4 ">
                  <div className="pisition-relative orderPizzaContainer my-auto">
                    {pizza}
                    <img
                      src="images/pizza-table.png"
                      className="d-block mx-auto pizzaPan centerAbsolute"
                      style={{
                        zIndex: 9,
                        width: "140px",
                        minWidth: "140px",
                        maxWidth: "140px",
                      }}
                    />
                  </div>
                </div>
                <div className="col col-lg-9 col-md-8 text-center my-4 px-4">
                  <div> نام پیتزای شما : </div>
                  <div>
                    <input
                      type="text"
                      className="textbox my-3 "
                      onBlur={this.onBlurHandler}
                    />
                  </div>
                </div>
              </div>
            </div>
            <div className="col col-lg-2 col-md-3 ">
              <div
                className="row orderBg rounded shadow my-2 ml-1"
                style={{ height: "100%" }}
              >
                <div className="col col-lg-12 my-auto text-center">
                  <CostNumber
                    price={this.props.globalPrice}
                    number={this.props.globalPizzaNumber}
                    itemPrice={this.props.globalPizzaPrice}
                    plusClicked={this.onPlusHandler}
                    minusClicked={this.onMinusHandler}
                  />
                </div>
              </div>
            </div>
          </div>
          {extraList}
          <div className="mx-3">
            <div className="row mx-3 my-5  justify-content-start ">
              <div className="col col-lg-2 col-md-3 text-left orderBg py-3 rounded-right">
                جمع سفارش :
              </div>
              <div className="col col-lg-2 col-md-3 text-right py-3 orderBg textPrice rounded-left">
                {toPersianDigit(this.props.globalPrice.toString())} تومان
              </div>
            </div>
          </div>
          <div className="row justify-content-between mx-5">
            <div className="col col-lg-2 col-sx-5">
              <Link to={process.env.PUBLIC_URL + "/pay"} className="link">
                <Button
                  text="  پرداخت  "
                  class="btn btn-block px-2 py-2 mt-4 mb-5 mx-auto btnNext rounded-pill"
                  disabled={this.state.prevDisabled}
                />
              </Link>
            </div>
            <div className="col col-lg-2 col-sx-5">
              <Link to={process.env.PUBLIC_URL + "/side-dish"} className="link">
                <Button
                  text="  مرحله قبل  "
                  class="btn btn-block px-2 py-2 mt-4 mb-5 mx-auto btnPrev rounded-pill"
                  disabled={this.state.prevDisabled}
                />
              </Link>
            </div>
          </div>
        </div>
        <br />
        <br />
        <br />
      </>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    globalPizza: state.pizza,
    globalExtra: state.extra,
    globalPrice: state.price,
    globalPizzaPrice: state.pizzaPrice,
    globalPizzaNumber: state.pizzaNumber,
    globalPizzaName: state.pizzaName,
  }; //reducer states
};

const mapDispatchToProps = (dispatch) => {
  return {
    OnIncPizzaNum: () => dispatch({ type: "INCPIZZANUM" }),
    OnDecPizzaNum: () => dispatch({ type: "DECPIZZANUM" }),
    OnNamePizza: (name) => dispatch({ type: "NAMEPIZZA", PizzaName: name }),
    OnIncExtraNum: (extra, price) =>
      dispatch({ type: "INCEXTRA", ExtraName: extra, ExtraPrice: price }),
    OnDecExtraNum: (extra, price) =>
      dispatch({ type: "DECEXTRA", ExtraName: extra, ExtraPrice: price }),
    OnRemoveExtr: (extra, price) =>
      dispatch({
        type: "REMOVEEXTRA",
        removedExtra: extra,
        removedExtraPrice: price,
      }),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Order);
