import React from "react";

const selectedIngredient = (props) => {
  const isSelected = props.isAvailable && props.isSelected ? "block" : "none";
  const isRunOut = props.isAvailable ? "none" : "block";
  const isClickable = !props.isSelected && props.isAvailable ? "none" : "block";
  const isExceeded =
    props.isExceeded && !props.isSelected ? "d-inline" : "d-none";

  return (
    <>
      <div className={isExceeded + " selectionExceeded "}></div>
      <div
        className=" selectedIngContainer rounded"
        style={{ display: isClickable }}
        onClick={props.clicked}
      >
        <div
          className="selectedMark selectedMarkGreen rounded-circle"
          style={{ display: isSelected }}
        >
          <i className="material-icons">done</i>
        </div>
        <div
          className="selectIngredient rounded-circle"
          style={{ display: isSelected }}
        ></div>
        <div
          className="selectedMark selectedMarkOrange rounded-circle"
          style={{ display: isSelected }}
          onClick={props.removed}
        >
          <i className="fa fa-trash-o"></i>
        </div>
        <div
          className="selectedMark selectedMarkMessage rounded-pill"
          style={{ display: isRunOut }}
        >
          ناموجود
        </div>
      </div>
    </>
  );
};

export default selectedIngredient;
