import React from "react";
import BannerTag from "./BannerTag";

const Banner2 = (props) => {
  if (props.step)
    return (
      <div className="d-none d-md-block bannerBg shadow">
        <div className="d-none d-md-flex flex-row justify-content-md-between align-items-center py-3 position-relative banner">
          <div className="bannerLine"></div>
          <BannerTag text="انتخاب پیتزا" index="1" step={props.step} />
          <BannerTag text="انتخاب افزودنی" index="2" step={props.step} />
          <BannerTag text="نهایی کردن سفارش" index="3" step={props.step} />
          <BannerTag text="پرداخت" index="4" step={props.step} />
        </div>
      </div>
    );
};

export default Banner2;
