import React, { Component } from "react";
import { connect } from "react-redux";
import Material from "./components/Ingredients";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Slider from "@niloofarf/react-slick";
import Button from "./components/Button";
import Banner2 from "./components/Banner2";
import * as ingredientNames from "./components/ingredientNames";
import { Link } from "react-router-dom";

const NextArrow = (props) => {
  const { className, style, onClick } = props;

  return (
    <div
      className={className + " rounded-circle material-icons"}
      style={{ ...style, display: "block" }}
      onClick={onClick}
    />
  );
};

const PrevArrow = (props) => {
  const { className, style, onClick } = props;
  return (
    <div
      className={className + " rounded-circle material-icons"}
      style={{ ...style, display: "block" }}
      onClick={onClick}
    />
  );
};

class SideDish extends Component {
  state = {
    price: 0,
    extra: [],
    CompSlideToShowF: 3,
    CompSlideToShowS: 3,
    width: 0,
    height: 0,
    slideWidthF: 588,
    slideWidthS: 588,
    justSwitchStep: true,
    sideDish: {
      frenchFriesOnlinePizza: { available: true, selected: false },
      frenchFries: { available: true, selected: false },
      saladCeasar: { available: true, selected: false },
      miranda: { available: true, selected: false },
      pepsi: { available: true, selected: false },
      sevenUp: { available: true, selected: false },
      bigBear: { available: true, selected: false },
      mirandaBig: { available: true, selected: false },
      sevenUpBig: { available: true, selected: false },
      PepsiBig: { available: true, selected: false },
    },
  };

  updateDimensions = () => {
    if (window.innerWidth > 1150) {
      this.setState({ CompSlideToShowS: 5, slideWidthS: 980 });
    } else if (window.innerWidth <= 1150 && window.innerWidth > 920) {
      this.setState({ CompSlideToShowS: 4, slideWidthS: 784 });
    } else if (window.innerWidth <= 920 && window.innerWidth > 740) {
      this.setState({
        CompSlideToShowS: 3,
        slideWidthS: 588,
        CompSlideToShowF: 3,
        slideWidthF: 588,
      });
    } else {
      this.setState({
        CompSlideToShowF: 2,
        slideWidthF: 392,
        CompSlideToShowS: 2,
        slideWidthS: 392,
      });
    }
  };

  componentDidMount() {
    window.addEventListener("resize", this.updateDimensions);
    if (window.innerWidth > 1150) {
      this.setState({ CompSlideToShowS: 5, slideWidthS: 980 });
    } else if (window.innerWidth <= 1150 && window.innerWidth > 920) {
      this.setState({ CompSlideToShowS: 4, slideWidthS: 784 });
    } else if (window.innerWidth <= 920 && window.innerWidth > 740) {
      this.setState({ CompSlideToShowS: 3, slideWidthS: 588 });
    } else {
      this.setState({
        CompSlideToShowF: 2,
        slideWidthF: 392,
        CompSlideToShowS: 2,
        slideWidthS: 392,
      });
    }
    if (this.props.globalExtra.length > 0) {
      const extra = [...this.props.globalExtra];
      const price = this.props.globalPrice;
      let sideDish = { ...this.state.sideDish };
      let extraState = [];
      for (let ext of extra) {
        extraState.push(ext.extraName);
        switch (ext.extraName) {
          case "frenchFriesOnlinePizza":
            sideDish.frenchFriesOnlinePizza.selected = true;
            break;
          case "frenchFries":
            sideDish.frenchFries.selected = true;
            break;
          case "saladCeasar":
            sideDish.saladCeasar.selected = true;
            break;
          case "miranda":
            sideDish.miranda.selected = true;
            break;
          case "pepsi":
            sideDish.pepsi.selected = true;
            break;
          case "sevenUp":
            sideDish.sevenUp.selected = true;
            break;
          case "bigBear":
            sideDish.bigBear.selected = true;
            break;
          case "mirandaBig":
            sideDish.mirandaBig.selected = true;
            break;
          case "sevenUpBig":
            sideDish.sevenUpBig.selected = true;
            break;
          case "PepsiBig":
            sideDish.PepsiBig.selected = true;
            break;
          default:
        }
      }
      this.setState({
        extra: extra,
        price: price,
        sideDish: sideDish,
      });
    } else {
      this.setState({ price: this.props.globalPrice });
    }
  }

  onClickHandler = (extr) => {
    this.setState({ justSwitchStep: false });
    const extrState = ingredientNames[extr].state;
    if (this.state.sideDish[extrState].available === false) {
      return;
    }
    const isSelectedBefore = this.state.extra.find((value) => value === extr);
    if (!isSelectedBefore) {
      if (!this.state.sideDish[extrState].selected) {
        const newExtra = {
          ...this.state.sideDish[extrState],
          selected: true,
        };
        this.setState({
          sideDish: {
            ...this.state.sideDish,
            [extrState]: newExtra,
          },
        });
        const extra = [...this.state.extra, extrState];
        this.setState((prevState) => {
          return {
            extra: extra,
            price: prevState.price + ingredientNames[extr].price,
          };
        });
        this.props.OnAddExtr(extr, ingredientNames[extr].price);
      }
    }
  };

  onRemoveHandler = (extr) => {
    const extrState = ingredientNames[extr].state;
    const newExtra = { ...this.state.sideDish[extrState], selected: false };

    const extra = [...this.state.extra];
    let index = extra.indexOf(extr);
    if (extra.length > 0 && index >= 0) {
      extra.splice(index, 1);
    }
    this.setState((prevState) => {
      return {
        sideDish: {
          ...prevState.sideDish,
          [extrState]: newExtra,
        },
        extra: extra,
        price: prevState.price - ingredientNames[extr].price,
      };
    });
    this.props.OnRemoveExtr(extr, ingredientNames[extr].price);
  };

  render() {
    const settings1 = {
      dots: true,
      infinite: false,
      speed: 500,
      slidesToShow: this.state.CompSlideToShowF > 2 ? 3 : 2,
      slidesToScroll: this.state.CompSlideToShowF > 2 ? 2 : 1,
      rtl: true,
      nextArrow: <NextArrow />,
      prevArrow: <PrevArrow />,
    };

    const settings2 = {
      dots: true,
      infinite: false,
      speed: 500,
      slidesToShow: this.state.CompSlideToShowS,
      slidesToScroll: this.state.CompSlideToShowS - 1,
      rtl: true,
      nextArrow: <NextArrow />,
      prevArrow: <PrevArrow />,
    };

    let extraPrice = null;
    //console.log(this.state.price);
    if (this.state.price > 0 || this.props.globalPrice > 0) {
      extraPrice = (
        <div className="price rounded mb-4 py-1">
          {this.state.price + " تومان "}
        </div>
      );
    }

    return (
      <>
        <Banner2 step="2" />
        <div className="bgGradient">
          <div style={{ marginLeft: "5%", marginRight: "5%" }}>
            <div
              style={{
                width: this.state.slideWidthS,
                marginLeft: "auto",
                marginRight: "auto",
              }}
            >
              <div className="stepTitle pt-5">سیب زمینی، سالاد، نوشابه</div>
              <div className="stepExp mb-4">
                در این مرحله می توانید از محصولات به سبد خرید خود اضافه کنید
              </div>
              <br />
              <div
                style={{
                  width: this.state.slideWidthF + "px",
                  marginLeft: "auto",
                  height: "auto",
                }}
              >
                <div className="footerInfo px-4 py-2">
                  انتخاب سیب زمینی و سالاد{" "}
                </div>
                <Slider
                  ref={(slider) => {
                    if (slider && this.state.justSwitchStep)
                      slider.slickGoTo(0);
                  }}
                  {...settings1}
                >
                  <Material
                    index={1}
                    img="1578404640403702x.jpg"
                    name="سیب زمینی آنلاین پیتزا"
                    isSelected={
                      this.state.sideDish.frenchFriesOnlinePizza.selected
                    }
                    isAvailable={
                      this.state.sideDish.frenchFriesOnlinePizza.available
                    }
                    isExceeded={false}
                    clicked={() =>
                      this.onClickHandler(
                        ingredientNames.frenchFriesOnlinePizza.name
                      )
                    }
                    removed={() =>
                      this.onRemoveHandler(
                        ingredientNames.frenchFriesOnlinePizza.name
                      )
                    }
                  />
                  <Material
                    index={2}
                    img="1602080669864992x.jpg"
                    name="سیب زمینی ساده"
                    isSelected={this.state.sideDish.frenchFries.selected}
                    isAvailable={this.state.sideDish.frenchFries.available}
                    isExceeded={false}
                    clicked={() =>
                      this.onClickHandler(ingredientNames.frenchFries.name)
                    }
                    removed={() =>
                      this.onRemoveHandler(ingredientNames.frenchFries.name)
                    }
                  />
                  <Material
                    index={3}
                    img="1591264401267602x.jpg"
                    name="سالاد سزار"
                    isSelected={this.state.sideDish.saladCeasar.selected}
                    isAvailable={this.state.sideDish.saladCeasar.available}
                    isExceeded={false}
                    clicked={() =>
                      this.onClickHandler(ingredientNames.saladCeasar.name)
                    }
                    removed={() =>
                      this.onRemoveHandler(ingredientNames.saladCeasar.name)
                    }
                  />
                </Slider>
              </div>
              <br />
              <br />
              <br />

              <div
                style={{
                  width: this.state.slideWidthS + "px",
                  marginLeft: "auto",
                  marginRight: "auto",
                  height: "auto",
                }}
              >
                <div className="footerInfo px-4 py-2">انتخاب نوشیدنی</div>
                <Slider
                  ref={(slider) => {
                    if (slider && this.state.justSwitchStep)
                      slider.slickGoTo(0);
                  }}
                  {...settings2}
                >
                  <Material
                    index={1}
                    img="1578404285367852x.jpg"
                    name="میراندا"
                    isSelected={this.state.sideDish.miranda.selected}
                    isAvailable={this.state.sideDish.miranda.available}
                    isExceeded={false}
                    clicked={() =>
                      this.onClickHandler(ingredientNames.miranda.name)
                    }
                    removed={() =>
                      this.onRemoveHandler(ingredientNames.miranda.name)
                    }
                  />
                  <Material
                    index={2}
                    img="1578404225402152x.jpg"
                    name="پپسی"
                    isSelected={this.state.sideDish.pepsi.selected}
                    isAvailable={this.state.sideDish.pepsi.available}
                    isExceeded={false}
                    clicked={() =>
                      this.onClickHandler(ingredientNames.pepsi.name)
                    }
                    removed={() =>
                      this.onRemoveHandler(ingredientNames.pepsi.name)
                    }
                  />
                  <Material
                    index={3}
                    img="1578404785856082x.jpg"
                    name="سون آپ"
                    isSelected={this.state.sideDish.sevenUp.selected}
                    isAvailable={this.state.sideDish.sevenUp.available}
                    isExceeded={false}
                    clicked={() =>
                      this.onClickHandler(ingredientNames.sevenUp.name)
                    }
                    removed={() =>
                      this.onRemoveHandler(ingredientNames.sevenUp.name)
                    }
                  />
                  <Material
                    index={4}
                    img="1578402784609582x.jpg"
                    name="بیک بر سیب"
                    isSelected={this.state.sideDish.bigBear.selected}
                    isAvailable={this.state.sideDish.bigBear.available}
                    isExceeded={false}
                    clicked={() =>
                      this.onClickHandler(ingredientNames.bigBear.name)
                    }
                    removed={() =>
                      this.onRemoveHandler(ingredientNames.bigBear.name)
                    }
                  />
                  <Material
                    index={5}
                    img="1579096807679152x.jpg"
                    name="سون آپ خانواده"
                    isSelected={this.state.sideDish.sevenUpBig.selected}
                    isAvailable={this.state.sideDish.sevenUpBig.available}
                    isExceeded={false}
                    clicked={() =>
                      this.onClickHandler(ingredientNames.sevenUpBig.name)
                    }
                    removed={() =>
                      this.onRemoveHandler(ingredientNames.sevenUpBig.name)
                    }
                  />
                  <Material
                    index={6}
                    img="1579096993792532x.jpg"
                    name="میراندا خانواده"
                    isSelected={this.state.sideDish.mirandaBig.selected}
                    isAvailable={this.state.sideDish.mirandaBig.available}
                    isExceeded={false}
                    clicked={() =>
                      this.onClickHandler(ingredientNames.mirandaBig.name)
                    }
                    removed={() =>
                      this.onRemoveHandler(ingredientNames.mirandaBig.name)
                    }
                  />
                  <Material
                    index={7}
                    img="1579096741799562x.jpg"
                    name="پپسی خانواده"
                    isSelected={this.state.sideDish.PepsiBig.selected}
                    isAvailable={this.state.sideDish.PepsiBig.available}
                    isExceeded={false}
                    clicked={() =>
                      this.onClickHandler(ingredientNames.PepsiBig.name)
                    }
                    removed={() =>
                      this.onRemoveHandler(ingredientNames.PepsiBig.name)
                    }
                  />
                </Slider>
                <br />
                <br />
                {extraPrice}
              </div>
              <br />
              <div className="row justify-content-between">
                <div className="col col-lg-3 col-sx-5">
                  <Link to={process.env.PUBLIC_URL + "/order"} className="link">
                    <Button
                      text="  ثبت نهایی پیتزا  "
                      class="btn btn-block px-2 py-2 mt-4 mb-5 mx-auto btnNext rounded-pill"
                      disabled={this.state.prevDisabled}
                    />
                  </Link>
                </div>
                <div className="col col-lg-3 col-sx-5">
                  <Link to={process.env.PUBLIC_URL + "/"} className="link">
                    <Button
                      text="  مرحله قبل  "
                      class="btn btn-block px-2 py-2 mt-4 mb-5 mx-auto btnPrev rounded-pill"
                      disabled={this.state.prevDisabled}
                    />
                  </Link>
                </div>
              </div>
              <br />
              <br />
              <br />
            </div>
          </div>
        </div>
      </>
    );
  }
}

const mapStateToProps = (state) => {
  return { globalExtra: state.extra, globalPrice: state.price }; //reducer states
};

const mapDispatchToProps = (dispatch) => {
  return {
    OnAddExtr: (extra, price) =>
      dispatch({
        type: "ADDEXTRA",
        newExtra: extra,
        newExtraPrice: price,
      }),
    OnRemoveExtr: (extra, price) =>
      dispatch({
        type: "REMOVEEXTRA",
        removedExtra: extra,
        removedExtraPrice: price,
      }),
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(SideDish);
