const initialState = {
  pizza: [],
  pizzaPrice: 0,
  price: 0,
  extra: [], //{extraName, extraNum}
  pizzaNumber: 1,
  pizzaName: "",
};

const reducer = (state = initialState, action) => {
  if (action.type === "ADD") {
    return {
      ...state,
      pizza: state.pizza.concat(action.newIngredient),
      price: state.price + action.newIngredientPrice,
      pizzaPrice: state.price + action.newIngredientPrice,
    };
  }
  if (action.type === "REMOVE") {
    const myState = { ...state };
    let myPizza = myState.pizza;
    let index = myPizza.indexOf(action.removedIngredient);
    if (myPizza.length > 0 && index >= 0) {
      myPizza.splice(index, 1);
    }
    return {
      ...state,
      pizza: myPizza,
      price: state.price - action.removedIngredientPrice,
      pizzaPrice: state.price - action.removedIngredientPrice,
    };
  }
  if (action.type === "ADDEXTRA") {
    const myState = { ...state };
    let myExtra = [...myState.extra];
    myExtra.push({ extraName: action.newExtra, extraNum: 1 });
    return {
      ...state,
      extra: myExtra,
      price: state.price + action.newExtraPrice,
    };
  }
  if (action.type === "REMOVEEXTRA") {
    const myState = { ...state };
    let myExtra = [...myState.extra];
    let newMyExtra = myExtra.filter((value) => {
      return value.extraName !== action.removedExtra;
    });
    // let index = myExtra.indexOf(action.removedExtra);
    // if (myExtra.length > 0 && index >= 0) {
    //   myExtra.splice(index, 1);
    // }
    return {
      ...state,
      extra: newMyExtra,
      price: state.price - action.removedExtraPrice,
    };
  }
  if (action.type === "INCPIZZANUM") {
    return {
      ...state,
      pizzaNumber: state.pizzaNumber + 1,
      price: state.price + state.pizzaPrice,
    };
  }

  if (action.type === "DECPIZZANUM") {
    return {
      ...state,
      pizzaNumber: state.pizzaNumber - 1,
      price: state.price - state.pizzaPrice,
    };
  }
  if (action.type === "INCEXTRA") {
    const myState = { ...state };
    let myExtra = [...myState.extra];
    let index = myExtra.findIndex(
      (value) => value.extraName === action.ExtraName
    );
    if (index >= 0) {
      myExtra[index].extraNum = myExtra[index].extraNum + 1;
    }
    return {
      ...state,
      extra: myExtra,
      price: state.price + action.ExtraPrice,
    };
  }
  if (action.type === "DECEXTRA") {
    const myState = { ...state };
    let myExtra = [...myState.extra];
    let index = myExtra.findIndex(
      (value) => value.extraName === action.ExtraName
    );
    if (index >= 0) {
      myExtra[index].extraNum = myExtra[index].extraNum - 1;
    }
    return {
      ...state,
      extra: myExtra,
      price: state.price - action.ExtraPrice,
    };
  }
  if (action.type === "NAMEPIZZA") {
    return {
      ...state,
      pizzaName: action.PizzaName,
    };
  }
  return state;
};

export default reducer;
